data: function() {
return {
allData: {},
orderId: '',
'isPaying': false,
confirmShow: false,
confirmShowCancel: false,
confirmShowReceive: false
}
},
created() {
this.orderId = this.$route.query.orderId
console.log(this.orderId)
if (this.orderId == '') {
tools.showError('没有此订单，即将放回上一页')
this.$router.go(-1)
}
this._getOrderDetailData()
},
