methods: {
gotoReturnGoods(rec_id) {

if (this.allData.order_status_code == 'WAITPAY') {
console.log(this.allData.order_status_desc)
return false
}
console.log(rec_id)
this.$router.push({'path': 'returngoods', query: {recId: rec_id}})
},
onConfirm() {
console.log('我要开始删除了', this.orderId)

let url = '/Mobile/Order/delete_order.html'
axios.get(url, {
params: {
id: this.orderId
}
})
.then((res) => {
console.log(res)
if (res.data.status == 1) {
this.confirmShow = false
/* this.allData.splice(this.nowIndex, 1) */
this.$router.go(-1)
tools.showError(res.data.msg)
} else {
tools.showError(res.data.msg)
}
})
.catch((error) => {
console.log(error)
})
},
onCancel() {
this.confirmShow = false
},
delOrder() {
this.confirmShow = true
},
onCancelReceive() {
this.confirmShowReceive = false
},
onConfirmReceive() {
console.log('我要开始确认收货了')

let url = '/Mobile/Order/order_confirm.html'
axios.get(url, {
params: {
id: this.orderId
}
})
.then((res) => {
console.log(res)
if (res.data.status == 1) {
this._getOrderDetailData()
this.confirmShowReceive = false
tools.showError(res.data.msg)
} else {
tools.showError(res.data.msg)
}
})
.catch((error) => {
console.log(error)
})
},
receiveOrder() {
this.confirmShowReceive = true
},
onConfirmCancel() {
console.log('我要开始取消订单了')

let url = '/Mobile/Order/cancel_order.html'
axios.get(url, {
params: {
id: this.orderId
}
})
.then((res) => {
console.log(res)
if (res.data.status == 1) {
this._getOrderDetailData()
this.confirmShowCancel = false
tools.showError(res.data.msg)
} else {
tools.showError(res.data.msg)
}
})
.catch((error) => {
console.log(error)
})
},
onCancelCancel() {
this.confirmShowCancel = false
},
cancelOrder() {
this.confirmShowCancel = true
},
goToPay() {
this.$refs.weixinPay.goToPay(this.orderId)
/* if (this.isPaying) {
return
}
this.isPaying = true
let url = '/Mobile/Payment/getCode.html'
