params: {
'pay_radio': 'pay_code=weixin',
'order_id': this.orderId
}
})
.then((res) => {
console.log(res)
if (res.data.status == 1) {
this.goWeixinPay(res.data.msg)
} else {
tools.showError(res.data.msg)
}
})
.catch((error) => {
console.log(error)
}) */
},
