gotoWuliu(id) {
this.$router.push({path: 'queryexpress', query: { orderId: id}})
},
_getOrderDetailData() {
let url = '/Mobile/Order/order_detail.html'
axios.post(url, {
'id': this.orderId
})
.then((res) => {
console.log(res)
if (res.data.status === 1) {


this.allData = res.data.data
}
})
.catch((error) => {
